.app-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  display: grid;
  grid-template-rows: 64px;
  grid-template-areas:
    'header'
    'content';
}

.app-bar {
  grid-area: 'header';
}

.content-area {
  grid-area: 'conent';
}



